import KLineData from '../../common/KLineData'
import { Indicator, IndicatorTemplate } from '../../component/Indicator'

interface Brar {
  br?: number
  ar?: number
}

/**
 * BRAR
 * é»˜è®¤å‚æ•°æ˜¯26ã€‚
 * å…¬å¼Næ—¥BR=Næ—¥å†…ï¼ˆHï¼CYï¼‰ä¹‹å’Œé™¤ä»¥Næ—¥å†…ï¼ˆCYï¼Lï¼‰ä¹‹å’Œ*100ï¼Œ
 * å…¶ä¸­ï¼ŒHä¸ºå½“æ—¥æœ€é«˜ä»·ï¼ŒLä¸ºå½“æ—¥æœ€ä½Žä»·ï¼ŒCYä¸ºå‰ä¸€äº¤æ˜“æ—¥çš„æ”¶ç›˜ä»·ï¼ŒNä¸ºè®¾å®šçš„æ—¶é—´å‚æ•°ã€‚
 * Næ—¥AR=(Næ—¥å†…ï¼ˆHï¼Oï¼‰ä¹‹å’Œé™¤ä»¥Næ—¥å†…ï¼ˆOï¼Lï¼‰ä¹‹å’Œ)*100ï¼Œ
 * å…¶ä¸­ï¼ŒHä¸ºå½“æ—¥æœ€é«˜ä»·ï¼ŒLä¸ºå½“æ—¥æœ€ä½Žä»·ï¼ŒOä¸ºå½“æ—¥å¼€ç›˜ä»·ï¼ŒNä¸ºè®¾å®šçš„æ—¶é—´å‚æ•°
 *
 */
const brar: IndicatorTemplate<Brar> = {
  name: 'BRAR',
  shortName: 'BRAR',
  calcParams: [26],
  figures: [
    { key: 'br', title: 'BR: ', type: 'line' },
    { key: 'ar', title: 'AR: ', type: 'line' }
  ],
  calc: (dataList: KLineData[], indicator: Indicator<Brar>) => {
    const params = indicator.calcParams
    let hcy = 0
    let cyl = 0
    let ho = 0
    let ol = 0
    return dataList.map((kLineData: KLineData, i: number) => {
      const brar: Brar = {}
      const high = kLineData.high
      const low = kLineData.low
      const open = kLineData.open
      const prevClose = (dataList[i - 1] ?? kLineData).close
      ho += (high - open)
      ol += (open - low)
      hcy += (high - prevClose)
      cyl += (prevClose - low)
      if (i >= params[0] - 1) {
        if (ol !== 0) {
          brar.ar = ho / ol * 100
        } else {
          brar.ar = 0
        }
        if (cyl !== 0) {
          brar.br = hcy / cyl * 100
        } else {
          brar.br = 0
        }
        const agoKLineData = dataList[i - (params[0] - 1)]
        const agoHigh = agoKLineData.high
        const agoLow = agoKLineData.low
        const agoOpen = agoKLineData.open
        const agoPreClose = (dataList[i - params[0]] ?? dataList[i - (params[0] - 1)]).close
        hcy -= (agoHigh - agoPreClose)
        cyl -= (agoPreClose - agoLow)
        ho -= (agoHigh - agoOpen)
        ol -= (agoOpen - agoLow)
      }
      return brar
    })
  }
}

export default brar
