import KLineData from '../../common/KLineData'
import { IndicatorStyle } from '../../common/Options'

import { formatValue } from '../../common/utils/format'

import { Indicator, IndicatorTemplate, IndicatorFigureStylesCallbackData } from '../../component/Indicator'

interface Macd {
  dif?: number
  dea?: number
  macd?: number
}

/**
 * MACDï¼šå‚æ•°å¿«çº¿ç§»åŠ¨å¹³å‡ã€æ…¢çº¿ç§»åŠ¨å¹³å‡ã€ç§»åŠ¨å¹³å‡ï¼Œ
 * é»˜è®¤å‚æ•°å€¼12ã€26ã€9ã€‚
 * å…¬å¼ï¼šâ’ˆé¦–å…ˆåˆ†åˆ«è®¡ç®—å‡ºæ”¶ç›˜ä»·12æ—¥æŒ‡æ•°å¹³æ»‘ç§»åŠ¨å¹³å‡çº¿ä¸Ž26æ—¥æŒ‡æ•°å¹³æ»‘ç§»åŠ¨å¹³å‡çº¿ï¼Œåˆ†åˆ«è®°ä¸ºEMA(12ï¼‰ä¸ŽEMA(26ï¼‰ã€‚
 * â’‰æ±‚è¿™ä¸¤æ¡æŒ‡æ•°å¹³æ»‘ç§»åŠ¨å¹³å‡çº¿çš„å·®ï¼Œå³ï¼šDIFF = EMA(SHORT) ï¼ EMA(LONG)ã€‚
 * â’Šå†è®¡ç®—DIFFçš„Mæ—¥çš„å¹³å‡çš„æŒ‡æ•°å¹³æ»‘ç§»åŠ¨å¹³å‡çº¿ï¼Œè®°ä¸ºDEAã€‚
 * â’‹æœ€åŽç”¨DIFFå‡DEAï¼Œå¾—MACDã€‚MACDé€šå¸¸ç»˜åˆ¶æˆå›´ç»•é›¶è½´çº¿æ³¢åŠ¨çš„æŸ±å½¢å›¾ã€‚MACDæŸ±çŠ¶å¤§äºŽ0æ¶¨é¢œè‰²ï¼Œå°äºŽ0è·Œé¢œè‰²ã€‚
 */
const movingAverageConvergenceDivergence: IndicatorTemplate<Macd> = {
  name: 'MACD',
  shortName: 'MACD',
  calcParams: [12, 26, 9],
  figures: [
    { key: 'dif', title: 'DIF: ', type: 'line' },
    { key: 'dea', title: 'DEA: ', type: 'line' },
    {
      key: 'macd',
      title: 'MACD: ',
      type: 'bar',
      baseValue: 0,
      styles: (data: IndicatorFigureStylesCallbackData<Macd>, indicator: Indicator, defaultStyles: IndicatorStyle) => {
        const { prev, current } = data
        const prevMacd = prev.indicatorData?.macd ?? Number.MIN_SAFE_INTEGER
        const currentMacd = current.indicatorData?.macd ?? Number.MIN_SAFE_INTEGER
        let color: string
        if (currentMacd > 0) {
          color = formatValue(indicator.styles, 'bars[0].upColor', (defaultStyles.bars)[0].upColor) as string
        } else if (currentMacd < 0) {
          color = formatValue(indicator.styles, 'bars[0].downColor', (defaultStyles.bars)[0].downColor) as string
        } else {
          color = formatValue(indicator.styles, 'bars[0].noChangeColor', (defaultStyles.bars)[0].noChangeColor) as string
        }
        const style = prevMacd < currentMacd ? 'stroke' : 'fill'
        return { style, color }
      }
    }
  ],
  calc: (dataList: KLineData[], indicator: Indicator<Macd>) => {
    const params = indicator.calcParams as number[]
    let closeSum = 0
    let emaShort: number
    let emaLong: number
    let dif = 0
    let difSum = 0
    let dea = 0
    const maxPeriod = Math.max(params[0], params[1])
    return dataList.map((kLineData: KLineData, i: number) => {
      const macd: Macd = {}
      const close = kLineData.close
      closeSum += close
      if (i >= params[0] - 1) {
        if (i > params[0] - 1) {
          emaShort = (2 * close + (params[0] - 1) * emaShort) / (params[0] + 1)
        } else {
          emaShort = closeSum / params[0]
        }
      }
      if (i >= params[1] - 1) {
        if (i > params[1] - 1) {
          emaLong = (2 * close + (params[1] - 1) * emaLong) / (params[1] + 1)
        } else {
          emaLong = closeSum / params[1]
        }
      }
      if (i >= maxPeriod - 1) {
        dif = emaShort - emaLong
        macd.dif = dif
        difSum += dif
        if (i >= maxPeriod + params[2] - 2) {
          if (i > maxPeriod + params[2] - 2) {
            dea = (dif * 2 + dea * (params[2] - 1)) / (params[2] + 1)
          } else {
            dea = difSum / params[2]
          }
          macd.macd = (dif - dea) * 2
          macd.dea = dea
        }
      }
      return macd
    })
  }
}

export default movingAverageConvergenceDivergence
